import { useCallback, useState } from "react";
import { useDropzone } from "react-dropzone";
import './fajltoltesform.css';

export const FajlFejltolt = () => {
    const [images, setImages] = useState([]); // Store multiple images

    const onDrop = useCallback((acceptedFiles) => {
      // Convert files to object URLs
      const newImages = acceptedFiles.map((file) => ({
        id: URL.createObjectURL(file), // Unique identifier
        file,
      }));
  
      setImages((prevImages) => [...prevImages, ...newImages]); // Add new images to state
    }, []);
  
    const removeImage = (id) => {
      setImages((prevImages) => prevImages.filter((image) => image.id !== id)); // Remove selected image
    };
  
    const { getRootProps, getInputProps } = useDropzone({
      onDrop,
      accept: "image/*",
      multiple: true, // Allow multiple file selection
    });
  
    return (
      <div className="feltolt" style={{ textAlign: "center" }}>
        {/* Drag-and-Drop Area */}
        <div
          {...getRootProps()}
          style={{
            border: "2px dashed #ccc",
            padding: 20,
            cursor: "pointer",
            marginBottom: 10,
          }}
        >
          <input {...getInputProps()} />
          <p className="feltolt_p">Ide húzza a hibáról a képeit, vagy kattintson ide, hogy kiválaszthassa őket. </p>
        </div>
  
        {/* Display Selected Images */}
        <div style={{ display: "flex", gap: 10, flexWrap: "wrap", justifyContent: "center" }}>
          {images.map((image) => (
            <div key={image.id} style={{ position: "relative", display: "inline-block" }}>
              <img src={image.id} alt="Preview" width="100" style={{ borderRadius: "5px" }} />
  
              {/* X Button to Remove Image */}
              <button className="torles"
                onClick={() => removeImage(image.id)}
                style={{
                  position: "absolute",
                  top: 5,
                  right: 5,
                  background: "red",
                  color: "white",
                  border: "none",
                  borderRadius: "50%",
                  width: "5px !important",
                  height: "20px",
                  cursor: "pointer",
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "center",
                  fontSize: "12px",
                }}
              >
                ✖
              </button>
            </div>
          ))}
        </div>
      </div>
    );
}

