import { useState } from "react";
import { Container, Row, Col } from "react-bootstrap";
import contactImg from "../assets/img/contact-img.svg";
import 'animate.css';
import TrackVisibility from 'react-on-screen';
import axios from 'axios';
import './Hibabejelentes.css';
import { FajlFejltolt } from "./fajltoltesform";

export const Hibajelentes = () => {
  const [egyszeruData, setEgyszeruData] = useState({
    teljesNev: '',
    email: '',
    telefonszam: '',
    hibaleiras: '',
    csatoltkepek: ''
  });


  const handleButtonClick = () => {
    setButtonText('Küldés...'); 
  };

  const handleChange = (e) => {
    const {name, value} = e.target;
    setEgyszeruData({...egyszeruData, [name]: value});
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      await axios.post('http://localhost:3001/hibajelentes', egyszeruData);
      alert('Email sent succesfully!');
      setButtonText('Elküldve')
    }
    catch(error){
      alert(`Error sending email! ${error}`);
    }

    setButtonText('Küldés...');
  };

 
  const [buttonText, setButtonText] = useState('Küldés');
  const [status, setStatus] = useState({});

  return (
    <section className="contact" id="hibabejelentes">
      <Container>
        <Row className="align-items-center">
          <Col size={12} md={6}>
            <TrackVisibility>
              {({ isVisible }) =>
                <img className={isVisible ? "animate__animated animate__zoomIn" : ""} src={contactImg} alt="Contact Us"/>
              }
            </TrackVisibility>
          </Col>
          <Col size={12} md={6}>
                <h2 >Hibabejelentés</h2>
                <form onSubmit={handleSubmit}>
      <Row>
        <Col size={12} sm={6} className="px-1">
          <input type="text" name="teljesNev" value={egyszeruData.teljesNev} placeholder="Teljes Név" onChange={handleChange} />
        </Col>
        <Col size={12} sm={6} className="px-1">
          <input type="email" name="email" value={egyszeruData.email} placeholder="E-mail cím" onChange={handleChange} />
        </Col>
        <Col size={12} sm={6} xl={12} className="px-1">
          <input type="tel" name="telefonszam" value={egyszeruData.telefonszam} placeholder="Telefonszám" onChange={handleChange} />
        </Col>
        <Col size={12} xs={12} className="px-1">
          <textarea rows="8" name="hibaleiras" value={egyszeruData.hibaleiras} placeholder="Hibaleírás" onChange={handleChange}></textarea>
        </Col>
        <Col size={12} sm={6} xl={12} className="px-1">
            <FajlFejltolt/>
        </Col>
        <Col size={12} className="px1">
          <div className="kuldes">
              <button onClick={handleButtonClick} className='kuldes-gomb'  type="submit"><span>{buttonText}</span></button>
          </div>
        </Col>
        
        {status.message && (
          <Col>
            <p className={status.success === false ? "danger" : "success"}>{status.message}</p>
          </Col>
        )}
      </Row>
    </form>
          </Col>
        </Row>
      </Container>
    </section>
  )
}
